<script context="module" lang="ts">
  type Variant = "primary" | "secondary" | "overlay" | "overlay-active" | "text" | "white" | "black"
  type Size = "sm" | "base" | "lg"

  export type { Variant, Size }
</script>

<script lang="ts">
  import Spinner from "../Spinner"
  import { cx } from "../utils"

  export let as = "button"
  export let variant: Variant = "primary"
  export let size: Size = "base"
  export let loading = false
  export let disabled = false

  export let count: number | null = null

  $: classes = cx($$restProps.class, "tint-button", variant, size, disabled && "disabled")
</script>

<!-- svelte-ignore a11y-click-events-have-key-events -->
<svelte:element
  this={as}
  {...$$restProps}
  class={classes}
  class:loading
  disabled={disabled || loading}
  on:click
>
  <span><slot /></span>
  {#if loading}<Spinner centered />{/if}
  {#if count !== null}<span class="count">{count}</span>{/if}
</svelte:element>

<style lang="postcss">
  .tint-button {
    @apply cursor-pointer text-center font-bold outline-none transition-[background-color,color,opacity];
  }

  .primary {
    @apply bg-[#16161a] text-white;
    &:hover {
      @apply bg-[#39393c];
    }
    &:active {
      @apply bg-[#515154];
    }
    &:disabled,
    &.disabled {
      @apply bg-[#b9b9ba];
      &:not(.loading) {
        @apply text-opacity-60;
      }
    }
  }
  .secondary {
    @apply bg-[#efefef] text-[#16161a];
    &:hover {
      @apply bg-[#e7e7e7];
    }
    &:active {
      @apply bg-[#dfdfdf];
    }
    &:disabled,
    &.disabled {
      @apply bg-[#f7f7f7];
      &:not(.loading) {
        @apply text-opacity-30;
      }
    }
  }

  .white {
    @apply border border-[#dcdcdd] bg-white text-[#16161a];
  }

  .black {
    @apply border border-[#16161a] bg-[#16161a] text-white;
  }

  .overlay {
    @apply bg-black bg-opacity-25 font-normal text-white;
  }

  .overlay-active {
    @apply bg-black bg-opacity-50 font-normal text-white;
  }

  .sm {
    @apply min-h-[2rem] rounded-2xl px-4 py-2 text-xs;
  }
  .base {
    @apply min-h-[2.25rem] rounded-2xl px-6 py-2 text-sm;
  }
  .lg {
    @apply min-h-[2.5rem] rounded-3xl px-10 py-[0.625rem] text-base;
  }

  .count {
    @apply absolute -top-2 -right-2 flex h-6 w-6 items-center justify-center rounded-full bg-[#ff2d55] text-white;
  }

  .loading {
    @apply relative;

    & > :global(:first-child) {
      @apply invisible;
    }
  }
</style>
